@import "default";

.pagination {
	text-align: center;
	padding: 20px 0;
	a:link,
	a:visited {
		color: $textColor; }
	a:hover,
	a:active {
		color: rgba($textColor, 0.8); }
	> a {
		min-width: 26px;
		padding: 0 5px;
		@include font-size2(16px, 30px);
		display: inline-block;
		text-align: center;
		margin-left: 3px;
		background: $pageBg;
		overflow: hidden;
		&:first-child {
			margin-left: 0; }
		&.active {
			background: $pageActiveBg;
			color: $pageHoverColor; }
		&.disabled {
			color: $pageDisabled; }
		&:hover {
			&:not([class*="disabled"]) {
				background: $pageHoverBg;
				color: $pageHoverColor; } } } }

.pagination2 {
	text-align: center;
	padding: 20px 0;
	a:link,
	a:visited {
		color: $textColor; }
	a:hover,
